.promo__wrapper {
  display: flex;
  width: 84.92%;
  max-width: $vr-1160;
  margin: 0 auto;
  margin-bottom: 50px;
  height: 100%;

  @include vp-1199 {
    min-width: $vr-678;
    width: 88.28%;
    min-height: 292px;
  }

  @include vp-767 {
    flex-direction: column;
    min-width: $vr-290;
    width: 90.62%;
    margin-bottom: 40px;
  }

  picture {
    width: clamp(100px, 56.9%, 660px);
    height: 100%;

    @include vp-767 {
      width: 100%;
      overflow: hidden;
      aspect-ratio: 1 / 1.01;
    }
  }

  img {
    aspect-ratio: 1.32 / 1;

    @include vp-767 {
      position: relative;
      top: 23px;
      height: 100%;
      object-fit: cover;
      object-position: 50% 0;
      transform: scale(1.16);
      aspect-ratio: initial;
    }
  }
}
